import { PortalBaseViewController } from '@/views/portal/PortalBaseViewController';
import { SignInController } from '@/views/portal/sign_in/SignInController';
import { TTAPI } from '@/services/TimeTrexClientAPI';

export class PortalJobVacancyViewController extends PortalBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#job_vacancy_view_container',

		} );

		super( options );
	}

	init( options ) {
		this.api = TTAPI.APIJobVacancyPortal;
		this.script_name = 'JobVacancyView';
		this.render();
		TTPromise.resolve( 'IndexController', 'onViewChange' );
	}

	search() {
		var $this = this;
		this.onSearchClick( {
			callback: function() {
				if ( LocalCacheData.getAllURLArgs().sm === 'ResetPassword' && LocalCacheData.getAllURLArgs().key && LocalCacheData.getAllURLArgs().company_id ) {
					var signInController = new SignInController();
				}
			}
		} );
	}

}
